@import '../../assets/styles/colors.scss';

.footer-section {
    margin: 0 8%;


    .footer-section__feedback-sections {
        width: 100%;
        border-top: 1px solid black;
        border-bottom: 1px solid black;

        .feedback-sections__content {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 3rem 0;

            .feedback-sections__text {
                display: flex;
                flex-direction: column;
                max-width: 675px;

                .feedback-sections__text-h1 {
                    font-family: 'Poppins';
                    font-size: 48px;
                    font-weight: 600;

                }

                .feedback-sections__text-p {
                    font-family: Poppins;
                    font-size: 14px;
                    font-weight: 400;
                    margin-bottom: 0;
                    color: #3F3F3F;
                }
            }

            .feedback-sections__form {
                display: flex;
                align-items: center;

                .feedback-sections__form-input {
                    height: 64px;
                    width: 532px;
                    padding-left: 1rem;
                    font-family: 'Poppins';
                    font-size: 16px;
                    border: 1px solid black;
                    border-radius: 60px;
                    
                    &:focus {
                        border: 1px solid #B3AAFE;
                    }

                    &::placeholder {
                        font-family: 'Poppins';
                        color: #4F5665;
                    }
                }

                .feedback-sections__form-btn {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-left: 1rem;

                    svg {
                        margin-left: 1rem;
                    }
                }
            }
        }

    }

    .footer-section-content {
        padding: 4rem 0 3rem 0;

        .footer-section-content__columns {
            display: grid;
            grid-template-columns: 4fr 2.5fr 2fr 2fr;
            gap: 10%;

            .section__column {

                .column-content {
                    display: flex;
                    align-items: flex-start;
                    flex-direction: column;
                    justify-content: space-between;
                    height: 100%;
                    .column-content__content {
                        .content-logo {
                            margin-bottom: 10%;
                            font-family: 'Poppins';
                            font-size: 24px;
                            font-weight: 600;
                            svg{
                                margin-right: 0.5rem;
                            }
                        }

                        .content-text {
                            font-family: 'Poppins';
                            font-size: 16px;
                            font-weight: 400;
                            color:black;
                        }
                    }

                    .column-content__copyright{
                        font-family: 'Poppins';
                        font-size: 16px;
                        font-weight: 600;
                        color:black;
                        span{
                            
                            margin-left: 0.4rem;
                        }
                    }
                }

                .column-contacts {
                    h3 {
                        color: #000;
                        margin-top: 0;
                        margin-bottom: 2.5rem;
                        font-family: 'Poppins';
                        font-size: 18px;
                        font-weight: 700;

                    }

                    .column-contacts__item {
                        display: flex;
                        align-items: center;
                        margin-bottom: 2rem;

                        .column-contacts__icon {
                            margin-right: 1rem;
                        }

                        .column-contacts__text {
                            color: #3F3F3F;
                            font-family: 'Poppins';
                            font-size: 14px;
                            font-weight: 600;
 
                            .column-contacts__text-p {
                                margin: 0;
                            }
                        }
                    }
                }

                .column-navigation {
                    h3 {
                        margin: 8% 0;
                        margin-top: 0;
                    }

                    .column-navigatin__list-links{
                        list-style-type: disc;
                        margin-left: 1.2rem;
                        color:#3F3F3F;
                        .column-navigatin-link{
                           margin:15% 0 ;
                           color:#3F3F3F;
                        }
                    }
                    
                }

                .column-social{
                    h3{
                        margin: 8% 0;
                        margin-top: 0;
                    }
                    .column-social__icons{
                        display: grid;
                        grid-template-columns: 1fr 1fr 1fr;
                        grid-template-rows: 1fr 1fr 1fr;
                        .social-icon{
                            margin: 0.75rem 0;
                            
                        }
                    }
                }
            }
        }
    }



}