.user-profile-section{
    margin: 4% 8%;
    width: 100%;
    align-self: center;
    position: relative;
    width: 550px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;



    .user-profile-settings{
        display: flex;
        align-items: center;

        .user-profile-container{
            display: flex;
            align-items: center;
            justify-content: space-between;
            border: 1px solid black;
            border-radius: 8px;
            width: calc(400px - 4rem);
            padding: 1rem 2rem
        }

        .user-profile-name{
            display: flex;
            align-items: center;
            span{
                margin-left: 1rem;
            }
        }

        svg{
            cursor: pointer;
        }
    }

    .user-profile-tabs{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        border: 1px solid black;
        border-radius: 8px;
        width: 400px;
        padding: 1rem 0;
        margin: 1rem 0;
        a{
            font-family: 'Poppins';
            font-size: 20px;
            font-weight: 500;
            display: flex;
            align-items: center;
            padding: 1rem 2rem;
            svg{
                margin-right: 0.7rem;
            }

        }
        

    }
    .user-profile-logout{
        display: flex;
        align-items: center;


        .user-profile-container{
            display: flex;
            align-items: center;
            border: 1px solid black;
            border-radius: 8px;
            width: calc(400px - 4rem);
            padding: 1rem 2rem;
            svg{
                margin-right: 1rem;
            }
        }
    }
}