@import '../../assets/styles/colors.scss';


.header {
    position: relative;

   

    .nav-links {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 140px;
        margin: 0 8%;
        .logo {
            a {
                display: flex;
                align-items: center;
                font-family: 'Poppins';
                font-size: 20px;
                font-weight: 600;
                line-height: 36px;
                letter-spacing: 0em;
                text-align: left;

                svg {
                    margin-right: 8px;
                }
            }
        }

        .default-links {
            position: relative;

            .header-list-links {
                display: flex;
                align-items: center;
                list-style: none;
                .list-link {
                    margin: 0 1rem;

                    font-family: 'Poppins';
                    font-size: 16px;
                    font-weight: 600;

                    .active {
                        color: #000;
                        border-bottom: 2px solid black;

                        svg {
                            path {
                                stroke: #000;
                            }
                        }
                    }

                    a {
                        color: #706E6E;
                        display: flex;
                        align-items: center;
                        border-bottom: 2px solid transparent;
                        padding: 0.3rem 0;
                        white-space: nowrap;

                        svg {
                            margin-left: 4px;
                        }
                    }

                }
            }

        }

        .header__search{
            display: flex;
            align-items: center;
            margin-left: 5rem;
            margin-right: 0.3rem;
            width: 100%;
            .header__search-input{
                width: 100%;
                border: none;
                border-bottom: 1px solid black;
                height: 24px;
                padding: 8px 0;
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 400;
                &:focus{
                    outline: none;
                }

                &::placeholder{
                    font-family: 'Poppins';
                    font-size: 16px;
                    font-weight: 400;

                }
            }
        }

        .icons-action {
            display: flex;
            align-items: center;

            .icons-action__element {
                display: flex;
                align-items: center;
                margin: 0 8px;
                cursor: pointer;
            }

            .icons-action__element-theme {
                display: flex;
                align-items: center;
                border: 1px solid black;
                border-radius: 60px;
                padding: 8px 16px;
                cursor: pointer;
                svg {
                    margin-right: 4px;
                }
            }
        }
    }
}



.header__search:not(.is-active) {
    pointer-events: none;
    display: none !important;
}

.is-active {
    opacity: 1 !important;
}

.display-none {
    display: none !important;
}