@import '../GlobalFilter.scss';


.filter-actions{

    .filter-actions__checkbox{
        margin: 10px 0;

        input {
            position: absolute;
            z-index: -1;
            opacity: 0;
            width: 18px;
            height: 18px;
            border-radius: 1px;
        }

        input+label {
            display: inline-flex;
            align-items: center;
            user-select: none;
        }

        input+label::before {
            content: '';
            display: inline-block;
            width: 18px;
            height: 18px;
            border-radius: 1px;
            flex-shrink: 0;
            flex-grow: 0;
            border: 1.2px solid #000;
            margin-right: 0.5em;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 60% 60%;

        }

        input:checked+label::before {
            background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='12' height='9' viewBox='0 0 12 9'%3e%3cpath fill='none' d='M0.907227 4.24987L4.18151 7.52416L10.7301 0.975586' stroke='black' stroke-width='1.5' stroke-miterlimit='10' /%3e%3c/svg%3e");
          }

        label {
            font-size: 16px;
            width: 100%;
            transition: all 0.15s linear;
            cursor: pointer;
        }
    }

    .filter-actions__checkbox:last-child{
        margin: 0;
    }
}