@import "../../assets/styles/colors.scss";

.crypto-statistics {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
}


.crypto-statistics__running-line {
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  cursor: default;
  
  .js-marquee-wrapper{
    width: auto !important;
    display: flex;
    align-items: center;
    .js-marquee{
      display: flex;
      align-items: center;
    }
  }

 

  .running-line__element {
    display: flex;
    align-items: center;
    font-size: 13px;

    .running-line__name {
      font-family: 'Poppins';
      font-size: 14px;
      font-weight: 700;
      color: #3F3F3F;
      text-transform: uppercase;
    }

    .running-line__price {
      color: #000;
      font-family: 'Poppins';
      font-size: 14px;
      font-weight: 700;
      margin: 0 0.5rem;
    }

    .running-line__discount {
      color: #3F3F3F;
      font-family: 'Poppins';
      font-size: 14px;
      font-weight: 700;
    }

    &::after {
      content: "";
      width: 5px;
      height: 5px;
      background: #3F3F3F;
      border-radius: 100px;
      margin: 0 2rem;
    }

  }
}
