@import '../../assets/styles/colors.scss';

.card {
    display: flex;
    flex-direction: column;
    width: 392px;
    height: auto;
    border-radius: 8px;
    border: 1px solid #000;

    .card-content {
        margin: 1.6rem 0 0 0;
        
        .card-top {
            margin: 0 2rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 8px;

            .card-top__label {
                display: flex;
                align-items: center;
                justify-content: center;
                background: #000;
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 600;
                color: #fff;
                width: 130px;
                height: 30px;
                border-radius: 3px;
            }

            .card-top__icon-actions {
                display: flex;
                align-items: center;

                .card-top__icon-actions__compare {
                    margin: 0 0.5rem;
                }

                .card-top__icon-actions__favorite {
                    margin: 0 0.5rem;
                }
            }
        }

        .card-images {
            margin: 0 2rem;
            margin-bottom: 24px;
            position: relative;

            .card-images__new-label {
                position: absolute;
                left: 0;
                top: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 55px;
                height: 30px;
                color: #706E6E;
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 600;
                border-radius: 3px;
            }

            .card-images__slider {
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }

        .card-name {
            margin: 0 2rem;
            .card-name__firm {
                font-family: 'Poppins';
                font-size: 17px;
                font-weight: 600;
                margin-bottom: 2px;
            }

            .card-name__text {
                font-family: 'Poppins';
                font-size: 21px;
                font-weight: 600;
            }

        }

        .card-price-status {
            margin: 0 2rem;
            margin-bottom: 16px;

            .card-price-status__price {
                font-family: 'Poppins';
                font-size: 32px;
                font-weight: 600;
            }

            .card-price-status__status {
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 600;
            }
        }

        .card-button{
            margin: 0 2rem;
            margin-bottom: 12px;
            .card-button__btn{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                svg{
                    margin-right: 0.6rem;
                }
            }
        }

        .card-info{

            .card-info__text{
                display: flex;
                align-items: center;
                justify-content: center;
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 500;
                color:#706E6E;
                margin-bottom: 12px;
            }

            .card-info__icons{
                padding: 15px;
                display: flex;
                justify-content: center;
                color:#706E6E;
                border-top:1px solid #706E6E;

                .card-info__icons-icon{
                    height: 20px;
                    margin: 0 1rem;
                    display: flex;
                    align-items: center;

                    .value{
                        margin-left: 0.3rem;
                        margin-right: 0.1rem;
                        font-family: 'Poppins';
                        font-size: 18px;
                        font-weight: 400;
                    }

                    .unit{
                        height: 100%;
                        display: flex;
                        align-items: flex-start;
                        font-family: 'Poppins';
                        font-size: 12px;
                        font-weight: 400;
                    }
                }
            }
        }
    }

}