@import '../../../assets/styles/colors.scss';

.login-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    .block__inputs {
        position: relative;
        display: flex;
        flex-direction: column;

        .block__inputs-input-password{
            position: relative;
            svg{
                path{
                    stroke: #4F5665;
                }
            }
        }

        .block__inputs-input__eye{
            position: absolute;
            background: none;
            border:none;
            right: 2%;
            top:15%;
            transform: translateY(50%);
            cursor: pointer;
        }
        
        .block__inputs-input{
            height: 54px;
            width: 328px;
            padding-left: 1rem;
            font-family: 'Poppins';
            font-size:16px;
            border: 1px solid black;
            border-radius: 60px;
            margin-bottom: 16px;

            &:focus{
                border: 1px solid #B3AAFE;
            }
            &::placeholder{
                font-family: 'Poppins';
                color:#4F5665;
            }
        }
    }

    .block__button {
        margin-bottom: 30px;
        .section-btn{         
            width: 348px;
        }
    }

    .block__forgot {
        margin-bottom: 24px;
        .block__forgot-p{
            margin: 0;
            font-family: 'Poppins';
            font-size: 14px;
            font-weight: 400;
            line-height: 17px;
            letter-spacing: 0em;
            padding-bottom: 4px;
            border-bottom: 1px solid #3F3F3F;
            color: #3F3F3F;
            cursor: pointer;
        }
    }
}