@import '../GlobalFilter.scss';

.filter-price-inputs {
    display: flex;
    align-items: center;

    .filter-price-input-label {
        font-size: 14px;
    }

    .filter-price-input {
        display: flex;
        justify-content: center;
        margin: auto;
        max-width: 7rem;

        input {
            border: none;
            background: transparent;
            border:1px solid #000;
            height: 19px;
            width: 124px;
            font-size: 18px;
            padding: 8px 16px;   
        }

        input:focus {
            outline: none;
        }

        input:active {
            outline: none;
        }
    }

}