@import '../../assets/styles/colors.scss';

.send-message-section{
    margin: 5% 10%;
    text-align: center;
    .send-message-section__content{
        .send-message-section__content-title{
            .content-title__p{
                margin-bottom: 1.5rem;
            }
            .content-title__button{
                display: flex;
                align-items: center;
                margin: auto auto;
                background:none;
                border: 1px solid #706E6E;
                border-radius: 60px;
                height: 48px;
                color:#4F5665;
                font-size: 16px;
                padding: 16px 12px;
                cursor: pointer;
                span{
                    display: flex;
                    align-items: center;
                    font-family: 'Poppins';
                    font-size:16px;
                    margin-left: 0.5rem;
                }
            }
        }
        .send-message-section__content-form{
            .content-form__inputs{
                display: flex;
                flex-direction: column;
                align-items: center;
                input{
                    width: 620px;
                    margin: 1rem 0;
                    padding: 16px;
                    border:1px solid #4F5665; 
                    border-radius: 40px;
                    font-size: 16px;
                    &::placeholder{
                        font-family: 'Poppins';
                        font-size: 16px;
                    }
                    // &:focus{
                    //     //outline: none;
                    // }
                }
                textarea{
                    margin: 1rem 0;
                    padding: 16px;
                    min-width: 620px;
                    max-width: 620px;
                    min-height: 140px;
                    max-height: 280px;
                    border:1px solid #4F5665; 
                    border-radius: 16px;
                    font-family: 'Poppins';
                    font-size: 16px;
                    &::placeholder{
                        font-family: 'Poppins';
                        font-size: 16px;
                    }
                    // &:focus{
                    //     outline: none;
                    // }
                }
            }
        }
    }
}