@import '../../assets/styles/colors.scss';

.products-section{
    margin: 0 8%;
    margin-top: 5%;
    font-family: 'Poppins';

    .products-section-top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 4%;
        width: 100%;
        border-left: 6px solid #000;

        .products-section-top__title{
           
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            font-family: 'Poppins';
            font-size: 48px;
            font-weight: 700;
            line-height: 40px;
            margin-left: 1rem;
            color: #000;

            .products-section-top__title__sub-text{
                margin-top: 0.5rem;
                font-size: 18px;
                font-weight: 400;
                line-height: 16px;
                color:#706E6E;
            }
        }
        .products-section-top__sort{
            display: flex;
            align-items: center;

            font-size: 18px;
            font-weight: 400;
            line-height: 21px;
            letter-spacing: 0em;
        }
    }

    .products-section-content{
        display: flex;
        align-items: flex-start;
        margin-bottom: 10%;

        .products-section-content__filters{
            margin-right: 24px;
        }
        .products-section-content__cards{
            display: grid;
            grid-template-rows: auto auto auto;
            grid-template-columns: auto auto auto;
            gap: 24px;
        }
    }
}
