@import '../../../assets/styles/colors.scss';

.modal-window {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
    width: 100%;
    pointer-events: none;
}

.modal-window-section {
    pointer-events: auto;
    position: relative;
    width: 100%;
    height: 100%;
    background: $primary-bg-color;
    width: 524px;
    height: auto;
    border-radius: 8px;
    
    .modal-window-section__top{
        margin: 10% 0 24px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .modal-window-section__top-title{
            font-family: 'Poppins';
            font-size: 18px;
            font-weight: 600;
            line-height: 21px;
            letter-spacing: 0em;
            color: #3F3F3F;
            margin-bottom: 20px;
        }
        .modal-window-section__top-select{
            display: flex;

            .modal-window-section__top-select__hr{
                height: auto;
                margin: 0;
                background: #B4B4B4;

            }
            .modal-window-section__top-select__btn{
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 600;
                border: none;
                background: none;
                padding: 0.5rem 1rem;
                margin: 0rem 1rem;
                border-bottom:  2px solid transparent;
            }
            .is-active-auth{
                color:#000;
                border-bottom: 2px solid #000;
            }
        }
    }
    .modal-window-section__bottom{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 53px;
        cursor: pointer;
        span{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 348px;
            height: 48px;
            border: 1px solid #BCC7DF;
            border-radius: 60px;

            font-family: 'Poppins';
            font-size: 14px;
            font-weight: 600;
            line-height: 17px;
            letter-spacing: 0em;
            text-align: left;
            color: #6180B1;
        }
    }

}

.modal-window-section__top-select__btn:not(.is-active-auth) {
    cursor: pointer;
    color: #B4B4B4;
    font-weight: 600;
}