.product-section-top{
    margin: 5% 8%;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 45% 55%;

    .product-section__gallery{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 50px;
        margin-right: 10rem;
    }

    .product-section__info{

        .product-section__info-name{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            height: auto;
            border-radius: 8px;
            margin-bottom: 16px;

            .product-section__info-name__firm-actions{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                .product-section__info-name__firm{
                    font-family: 'Poppins';
                    font-size: 18px;
                    font-weight: 600;
                }
                
                .product-section__info-name__actions{
                    display: flex;
                    align-items: center;
                    .card-top__icon-actions__compare{
                        margin: 0 10px;
                        cursor: pointer;
                    }
                    .card-top__icon-actions__favorite{
                        margin: 0 10px;
                        cursor: pointer;
                    }
                }
            }
            .product-section__info-name__name{
                font-family: 'Poppins';
                font-size: 32px;
                font-weight: 600;
            }
        }

        .product-section__info-price{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            height: auto;
            padding-bottom: 24px;
            border-bottom: 1px solid #B4B4B4;
            margin-bottom: 24px;
            .product-section__info-price__price{
                font-family: 'Poppins';
                font-size: 40px;
                font-weight: 600;
            }
            .product-section__info-price__available{
                font-family: 'Poppins';
                font-size: 18px;
                font-weight: 600;
                color:#706E6E;
            }
        }

        .product-section__info-characteristics{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            border-radius: 8px;
            margin-bottom: 16px;
            .product-section__info-characteristics__characteristics{
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 1rem 0;
                width: 75%;
                .product-section__info-characteristics__characteristics-name{
                    font-family: 'Poppins';
                    font-size: 18px;
                    font-weight: 600;
                    color:#3F3F3F;
                }
                .product-section__info-characteristics__characteristics-value{
                    font-family: 'Poppins';
                    font-size: 18px;
                    font-weight: 500;
                    color:#B4B4B4;                
                }
            }
            .product-section__info-characteristics__counts-buttons{
                margin: 1rem 0;
                .product-section__info-characteristics__counts-buttons__counts{
                    font-family: 'Poppins';
                    font-size: 20px;
                    font-weight: 600;
                    .counts-buttons__counts-plus{
                        padding: 5px 12px;
                        border: 1px solid #B4B4B4;
                        border-right: none;
                        border-radius: 3px 0px 0px 3px;
                        color:#B4B4B4;
                        cursor: pointer;
                    }
                    .counts-buttons__counts-count{
                        padding: 5px 20px;
                        width: 100px;
                        border: 1px solid #B4B4B4;
                        border-right: none;
                        color:#B4B4B4;
                    }
                    .counts-buttons__counts-minus{
                        padding:  5px 12px;
                        border-radius: 0px 3px 3px 0px;
                        border-left: none;
                        border: 1px solid #B4B4B4;
                        color:#B4B4B4;
                        cursor: pointer;
                    }
                }
            }
        }

        .product-section__info-buttons{
                display: flex;
                align-items: center;
                margin-top: 2.5rem;
            .product-section__info-buttons-buy{
                display: flex;
                align-items: center;
                justify-content: center;
                svg{
                    margin-right: 0.6rem;
                }
            }

            .product-section__info-buttons-quick-order{
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: 1rem;
                background-color: transparent;
                border: 2px solid #000;
                border-radius: 30px;
                color:#000;
            }
        }

    }
}


.product-section-guarantee{
    border-top:1px solid #B4B4B4;
    padding-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    .product-section-guarantee__list{
        margin: 0% 15%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        .product-section-guarantee__element{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 180px;
            height: 220px;
            h1{
                font-family: 'Poppins';
                font-size: 16px;
                font-weight: 600;
                margin-bottom: 0;
                text-align: center;
            }
            p{
                font-family: 'Poppins';
                font-size: 12px;
                font-weight: 300;
                color:#706E6E;
                margin-top: 5px;
                text-align: center;
            }
        }
    }

}

.product-section-main-info{
    margin: 3% 8%;
    .product-section-main-info__tabs-buttons{
        .product-section-main-info__tabs-button{
            padding: 1rem 2rem;
            background:none;
            border: 1px solid #B4B4B4;
            border-bottom: none;
            border-radius: 8px 8px 0 0;
            font-family: 'Poppins';
            font-size: 22px;
            font-weight: 500;
            color:#B4B4B4
        }

        .product-active-tabs{
            border: 1px solid #000;
            border-bottom: none;
            font-family: 'Poppins';
            font-size: 22px;
            font-weight: 500;
            color:#000
        }
    }
    .product-section-main-info__tabs-content{
        width: 100%;
        border: 1px solid #000;
        padding: 2rem 1rem;
        p{
            margin: 10px 0;
        }
        ul{
            list-style-type: disc;
            margin-left: 2rem;
        }
    }
}