@import '../../assets/styles/colors.scss';



.introduction-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 2% 8%;

    .introduction-section__content {
        width: 481px;

        .introduction-section__content-h1 {
            margin-left: 10px;
        }

        .introduction-section__content-btn {
            display: flex;
            align-items: center;
        }
    }

    .introduction-section__img {
        width: 898px;
        height: 564px;
        background: #E7EAED;
        border-radius: 8px;

    }
}

.crypto-section {
    margin: 4% 0;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
}

.banner-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 4% 8%;

    .banner-section-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        border-left: 6px solid black;
        margin-bottom: 2rem;

        .banner-section-top__text {
            padding-left: 0.5rem;

            .banner-section-top__text-h1 {
                font-family: 'Poppins';
                font-size: 48px;
                font-weight: 600;
                line-height: 35px;
                margin: 0;
                margin-bottom: 1rem;
                padding: 0;
            }

            .banner-section-top__text-p {
                font-family: 'Poppins';
                font-size: 18px;
                font-weight: 400;
                line-height: 18px;
                color: #706E6E;
                margin: 0;
            }
        }

        .banner-section-top__link {
            display: flex;
            align-items: center;
            border-bottom: 2px solid #706E6E;

            a {
                font-family: 'Poppins';
                font-size: 24px;
                font-weight: 500;
                color: #424040;
            }

            svg {
                margin-left: 0.5rem;
            }

        }
    }

    .banner-section-content {
        position: relative;
        display: grid;
        gap: 15px;
        grid-template-areas:
            'main . .'
            'main . .';
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        width: 100%;
        height: 576px;

        .banner-section-content__element_main {
            position: relative;
            grid-area: main;
            border: 1px solid #3F3F3F;
            border-radius: 8px;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: space-between;
            font-family: 'Poppins';
            font-size: 18px;
            font-weight: 600;

            .top-content {
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                height: 100%;
                width: 100%;
                background: linear-gradient(180deg, #3E3D3D 0%, #000000 100%);
                border-radius: 6px 6px 0 0;

                .top-content-content {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    
                    height: 100%;
                    margin-left: 4rem;

                    .top-content__icons {
                        svg:not(:first-child) {
                            margin: 0 10px;
                        }
                    }

                    .top-content__text {
                        display: flex;
                        flex-direction: column;
                        font-family: 'Poppins';
                        color: #fff;

                        p {
                            margin: 5px 0px;
                        }

                        .top-content__text__fabricator {
                            font-size: 24px;
                        }

                        .top-content__text__text {
                            display: flex;
                            align-items: center;
                            font-size: 16px;
                        }

                        .top-content__text__name {
                            font-size: 32px;
                            font-weight: 600;
                        }

                        .top-content__text__W {
                            font-size: 16px;
                        }

                        .top-content__text__price {
                            font-size: 32px;
                            font-weight: 600;
                        }
                    }

                    .top-content__button-btn {
                        margin-top: 15px;
                        font-family: 'Poppins';
                        font-size: 18px;
                        font-weight: 600;
                        color: #fff;
                        background: transparent;
                        border: 2px solid #fff;
                        border-radius: 60px;
                        width: 175px;
                        height: 53px;
                        cursor: pointer;
                    }
                }

                .top-content-img {
                    display: flex;
                    align-items: flex-end;
                    justify-content: flex-end;
                    height: 100%;

                    img {
                        width: 420px;
                        height: 420px;
                    }
                }
            }

            .bottom-content {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 3.3rem;

                svg {
                    margin-left: 1rem;
                }
            }
        }

        .banner-section-content__element {
            position: relative;
            border: 1px solid #3F3F3F;
            border-radius: 8px;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: space-between;

            .top-content {
                position: relative;
                display: flex;
                justify-content: space-between;
                height: 100%;
                width: auto;
                margin-left: 1.5rem;

                .top-content-content {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    width: 100%;
                    height: 100%;

                    .top-content__icons {
                        svg {
                            margin: 0 3px;
                        }
                    }

                    .top-content__text {
                        display: flex;
                        flex-direction: column;
                        font-family: 'Poppins';

                        p {
                            margin: 1px 5px;
                        }

                        .top-content__text__fabricator {
                            font-size: 14px;

                        }

                        .top-content__text__text {
                            font-size: 10px;
                            display: flex;
                            align-items: center;
                            color: #706E6E;
                        }

                        .top-content__text__name {
                            font-size: 16px;
                            font-weight: 600;
                        }

                        .top-content__text__W {
                            font-size: 16px;
                        }

                        .top-content__text__price {
                            font-size: 16px;
                            font-weight: 600;
                        }
                    }

                    .top-content__button-btn {
                        margin-top: 3px;
                        font-family: 'Poppins';
                        font-size: 12px;
                        font-weight: 600;
                        color: #000;
                        background: transparent;
                        border: 1px solid #000;
                        border-radius: 60px;
                        width: 90px;
                        height: 28px;
                        cursor: pointer;
                    }
                }

                .top-content-img {
                    display: flex;
                    align-items: flex-end;
                    justify-content: flex-end;
                    height: 100%;

                    img {
                        width: 200px;
                        height: 200px;
                    }
                }
            }

            .bottom-content {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 2.5rem;
                border-top: 1px solid #000;
                border-radius: 0 0 6px 6px;
                background: #000;
                color: #fff;

                svg {
                    margin-left: 1rem;
                }
            }
        }

    }
}

.sponsors-section {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    padding: 1rem 0;

    .sponsors-section__img {
        display: flex;
        align-items: center;


        &:not(:last-child)::after {
            content: "";
            width: 5px;
            height: 5px;
            background: #3F3F3F;
            border-radius: 100px;
            margin: 0 44px;

        }

        img {
            height: 56px;
            filter: grayscale(1);
            transition: all 0.3s;
            cursor: pointer;

            &:hover {
                filter: grayscale(0);
                transition: all 0.3s;
            }
        }
    }

}

.reviews-section {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5% 10%;

    .reviews-section__title {
        text-align: center;
        width: 530px;
    }
}